<template>
  <el-row>
    <el-col :span="24" class="m-top-md">
      <div class="grid-content bg-purple-dark">
        <el-card shadow="hover">
          <div slot="header" class="clearfix">
            <span>{{data.title}}</span>
          </div>
          <el-table border size="mini" class="tb-edit" :data="data.list" highlight-current-row>
            <el-table-column prop="title" label="标签">
              <template slot-scope="scope">
                <el-input
                  size="mini"
                  v-if="scope.row.isEdit"
                  style="width: 100%"
                  v-model="scope.row.title"
                  placeholder="请输入内容">
                </el-input>
                <span v-else>{{scope.row.title}}</span>
              </template>
            </el-table-column>
            <el-table-column prop="start" label="从（）">
              <template slot-scope="scope">
                <el-input
                  size="mini"
                  v-if="scope.row.isEdit"
                  style="width: 100%"
                  v-model="scope.row.start"
                  placeholder="请输入内容">
                </el-input>
                <span v-else>{{scope.row.start}}</span>
              </template>
            </el-table-column>
            <el-table-column prop="end" label="至（不含）">
              <template slot-scope="scope">
                <el-input
                  size="mini"
                  v-if="scope.row.isEdit"
                  style="width: 100%"
                  v-model="scope.row.end"
                  placeholder="请输入内容">
                </el-input>
                <span v-else>{{scope.row.end}}</span>
              </template>
            </el-table-column>
            <el-table-column
              fixed="right"
              label="操作"
              width="100">
              <template slot-scope="scope">
                  <el-tooltip class="item" effect="dark" content="编辑" placement="top">
                      <el-button
                      size="small"
                      type="text"
                      @click="edit(scope.row.id)"
                      icon="iconfont icon-bianji"
                      v-show="currentEdit!== scope.row.id">
                    </el-button>
                  </el-tooltip>
                  <el-tooltip class="item" effect="dark" content="保存" placement="top">
                      <el-button
                      size="small"
                      type="text"
                      @click="save"
                      style="margin-left: 0;"
                      icon="iconfont icon-fuzhi"
                      v-show="currentEdit === scope.row.id">
                    </el-button>
                  </el-tooltip>
                  <el-tooltip class="item" effect="dark" content="删除" placement="top">
                      <el-button
                      @click.native.prevent="removeRecord(scope.row.id)"
                      type="text"
                      size="small"
                      icon="iconfont icon-iconfont-shanchu"
                      style="color: #FE2B6F">
                    </el-button>
                  </el-tooltip>
              </template>
            </el-table-column>
          </el-table>
            <el-button
              :style="specialTableBtn"
              icon="el-icon-plus"
              @click="addRecord"
              type="primary">
              新增行
            </el-button>
        </el-card>
      </div>
    </el-col>
  </el-row>
</template>

<script>
import {generateUUID} from '@/utils/uuid'

export default {
  name: 'RangeDimension',
  props: {
    data: {
      type: Object,
      required: true
    }
  },
  data () {
    return {
      currentEdit: '',
      specialTableBtn: {
        width: '100%',
        marginTop: '5px',
        border: '1px dashed #15afff',
        background: '#fff',
        color: '#15afff',
        display: 'block'
      }
    }
  },
  computed: {
    currentEditIndex () {
      return this.data.list.findIndex(item => item.id === this.currentEdit)
    }
  },
  methods: {
    addRecord () {
      const id = generateUUID()
      const newItem = {id: id, isEdit: false, title: '', start: '', end: ''}
      this.data.list.push(newItem)
      this.edit(id)
    },
    removeRecord (id) {
      this.$confirm('确定删除此条数据记录吗?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        const index = this.data.list.findIndex(item => item.id === id)
        if (index === this.currentEditIndex) {
          this.currentEdit = ''
        }
        this.data.list.splice(index, 1)
        this.$message({
          type: 'success',
          message: '删除成功!'
        })
      }).catch(() => {
      })
    },
    edit (id) {
      if (this.currentEditIndex > -1) {
        this.data.list[this.currentEditIndex].isEdit = false
      }
      this.currentEdit = id
      this.data.list[this.currentEditIndex].isEdit = true
    },
    save () {
      this.data.list[this.currentEditIndex].isEdit = false
      this.currentEdit = ''
    }
  }
}
</script>

<style scoped>
  div >>> .el-card__header{
    background-color: #1ea8f0;
    color: #ffffff
  }
</style>



